// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import 'core-js/es6/promise'
import 'core-js/es6/string'
import 'core-js/es7/array'
// import cssVars from 'css-vars-ponyfill'
import Vue from 'vue'
import axios from 'axios'
import VueAxios from 'vue-axios'
import BootstrapVue from 'bootstrap-vue'
import App from './App'
import router from './router/shopify'
import store from './store'

Vue.use(VueAxios, axios)
Vue.use(BootstrapVue)
async function getToken() {
  var response = await axios.get(`${process.env.VUE_APP_SHOPIFY_BOUGHT_TOGETHER_URL}/token/token`, {
    withCredentials: true
  });
  return response.data.access_token;
}

var access_token = null;
// Adding token to each axios request
axios.interceptors.request.use(async function (config) {
  try {
    if (access_token != null)
      config.headers.Authorization = `Bearer ${access_token}`;
    return config;
  }
  catch (err) {
    return Promise.reject(err)
  }
});

// Checking for user auth before each request. Challenging if not.
router.beforeEach((to, from, next) => {
  //check for expired token 
  next();
});

axios.interceptors.response.use(
  (response) => {
    return response
  },
  async (error) => {
    if (error.response.status === 401) {
      try {
        const originalRequest = error.config
        // token expired
        access_token = await getToken();
        originalRequest.headers['Authorization'] = access_token;
        return axios(originalRequest)
      }
      catch (error) {
        debugger
        window.location.href = `${process.env.VUE_APP_SHOPIFY_BOUGHT_TOGETHER_URL}/Home/Admin`
        return Promise.reject(error);
      }
    }
    return Promise.reject(error)
  });

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  template: '<App/>',
  store: store,
  components: {
    App
  }
})


