import Vue from 'vue'
import Router from 'vue-router'

const BoughtTogetherApp = () => import('@/views/shopify/bought-together-app/BoughtTogetherApp')

Vue.use(Router)


export default new Router({
  mode: 'hash', // https://router.vuejs.org/api/#mode
  linkActiveClass: 'open active',
  scrollBehavior: () => ({ y: 0 }),
  routes: [
    {
      path: '/app/bought-together',
      name: 'Frequently Bought Together App',
      component: BoughtTogetherApp
    },
    {
      //default path is install path
      path: '/',
      beforeEnter(to, from, next) {
        window.location = `${process.env.VUE_APP_SHOPIFY_BOUGHT_TOGETHER_URL}/App/Install${window.location.search}`
      }
    }
  ]
});
